.App {
  text-align: center;
  margin-top: 50px;
  background-color: #FFFFFF80;
}

p {
  color: #7b7d8e;
}

ul {

  color: #7b7d8e;
  text-align: center;
}

li {
  text-align: center;
  list-style-type: none;

}

h2 {
  color: #000551;
  margin-bottom: 20px;
}

h3 {
  background-color: #fc9700;
  padding: 20px 4px;
  border-radius: 12px;
  color: aliceblue;
}

h4 {
  color: #000551;
}



form {
  margin: 20px auto;
  width: 300px;
}

button {
  display: block;
  margin: 20px auto;
  padding: 20px 40px;
  background-color: #19229c;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 12px;
  width: 300px;
  font-size: 16px;
}

button:hover {
  background-color:  #5660e6;
}


.custom-file-input {
  opacity: 0;
  position: relative;
  z-index: 2;
}


.custom-file-label {
  height: 300px;
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px dashed #ffe2b9;
  border-radius: 20px;
  cursor: pointer;
  position: relative;
  z-index: 1;
}

.custom-file-label::after {
  content: "Upload Handwritten Character for prediction";
  color: #fc9700;
}

